.logo {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.logo--footer {
  @media (max-width: $mobile-width) {
    & .logo__text {
      display: flex;
    }

    & .logo__image {
      width: 52px;
      height: 52px;
    }
  }
}

.logo__text {
  font-family: $RobotoCondensed;
  text-decoration: none;
  color: $white;
  font-weight: 700;
  font-size: 25px;
  line-height: 29.3px;
  display: flex;
  flex-direction: column;

  padding-bottom: 8px;

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.logo__subtitle {
  font-family: $Roboto;
  color: $dark-white;
  font-size: 12px;
  line-height: 14px;
}

.logo__image {
  margin-right: 16px;
  margin-top: 8px;

  @media (max-width: $mobile-width) {
    width: 38px;
    height: 38px;
  }
}

.logo__title--footer {
  color: $logo-color;
}
